.switcher__radio {
  width: 20px;
  height: 20px;
  -ms-appearance: none;
  appearance: none;
  cursor: pointer;
  outline: 0;
}

.switcher__bg {
  width: 100%;
  height: 100%;
  pointer-events: none;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switcher__status {
  width: 18px;
  height: 18px;
  pointer-events: none;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.switcher__radio--dark:checked ~ .switcher__status {
  transform: translateX(20px);
}

.switcher__radio--dark:checked ~ .switcher__bg {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.switcher__radio:focus-visible ~ .switcher__status {
  box-shadow: 0 0 0 2px #000;
}

